import React, { useState } from 'react';
import "./item.css";

export const ItemForm = ({ onSubmit }) => {
  const [from, setFrom] = useState("");
  var curr = new Date();
  var date = curr.toISOString().substring(0, 10);
  return (
    <form onSubmit={onSubmit}>
      <div className="form-group">
        <label htmlFor="transaction_type">Transaction Type</label>
        <select name="transaction_type" id="transaction_type"
          onChange={(e) =>
            e.target.value === "Income" ? setFrom("Chequing") : null}>
          <option value="Spend">Spend</option>
          <option value="Income">Income</option>
          <option value="Transfer">Transfer</option>
        </select>
      </div>
      <div>
        <label htmlFor="name">Name</label>
        <input className="form-control" id="name" required />
      </div>
      <div className="form-group">
        <label htmlFor="amount">Amount</label>
        <input
          type="number"
          step=".01"
          id="amount"
          required
        />
      </div>
      <div className="form-group">
        <label htmlFor="from">From</label>
        <select name="from" id="from" value={from} onChange={
          (e) => { setFrom(e.target.value) }}>
          <option value=""></option>
          <option value="Chequing">Chequing</option>
          <option value="Plane Tickets">Plane Tickets</option>
          <option value="Hobbies">Hobbies</option>
          <option value="Evette">Evette</option>
          <option value="Investments">Investments</option>
        </select>
      </div>
      <div className="form-group">
        <label htmlFor="to">To</label>
        <select name="to" id="to" >
          <option value=""></option>
          <option value="Chequing">Chequing</option>
          <option value="Plane Tickets">Plane Tickets</option>
          <option value="Hobbies">Hobbies</option>
          <option value="Evette">Evette</option>
          <option value="Investments">Investments</option>
        </select>
      </div>
      <div>
        <label htmlFor="date">Date</label>
        <input
          type="date"
          id="date"
          defaultValue={date}
          required
        />
      </div>
      <div className="form-group">
        <label htmlFor="budget_item">Budget Item</label>
        <select name="budget_item" id="budget_item">
          <option value=""></option>
          <option value="Food">Food</option>
          <option value="Health">Health</option>
          <option value="Social">Social</option>
          <option value="Car">Car</option>
          <option value="Gifts">Gifts</option>
          <option value="Commuting">Commuting</option>
          <option value="Subscription">Subscription</option>
          <option value="Utilities">Utilities</option>
          <option value="Other">Other</option>
        </select>
      </div>
      <div>
        <label htmlFor="notes">Notes</label>
        <input className="form-control" id="notes" />
      </div>
      <div className="form-group">
        <button className="form-control btn btn-primary" type="submit">
          Submit
        </button>
      </div>
    </form>
  );
};
export default ItemForm;
