import './App.css';
import { Table } from './Table/Table';
import Password from './Password/Password';
import { getCookie } from './cookies';
import Header from './Header/header'
import {
  Routes,
  Route,
} from "react-router-dom";

function App() {
  let unlocked = getCookie("unlocked");
  return (
    <div>
      {unlocked ?
        <div>
          <Header/>
          <Routes>
            <Route path="*"
              element={<Table />} />
          </Routes>
        </div>
        :
        <Password></Password>}

    </div>
  );
}

export default App;
