import React from 'react';
import { NavLink } from 'react-router-dom';
import NewItemButton from '../New_Item/new_item_button';
import './header.css'

const Header = (props) => {
    return (
        <div className="navbar">
            <NavLink className='navItem' to={{
                pathname: '/'
            }}><header >Stinker's Budget</header></NavLink>
            <NavLink to={{
                pathname: '/'
            }} className='navItem navButton'>Cashflow</NavLink>
            <NewItemButton className='navItem newItem'></NewItemButton>
        </div>
    )
}
export default Header;