import { db, db_name } from "../Database/Firebase";
import { doc, updateDoc, deleteDoc } from "firebase/firestore";
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css

export const EditCell = ({ row, table }) => {
    const meta = table.options.meta;

    const setEditedRows = async (e) => {
        const elName = e.currentTarget.name;
        meta?.setEditedRows((old) => ({
            ...old,
            [row.id]: !old[row.id],
        }));
        if (elName !== 'edit') {
            meta?.revertData(row.index, e.currentTarget.name === 'cancel');
        }
        if (elName === 'done') {
            const { ['id']: omitted, ...changes } = row.original;
            const itemRef = doc(db, db_name, row.original.id);

            await updateDoc(itemRef, changes);
        }
    };
    const confirmDelete = (e) => {
        confirmAlert({
            title: 'Confirm to submit',
            message: 'Are you sure to do this.',
            buttons: [
                {
                    label: 'Yes',
                    onClick: () => deleteRows()
                },
                {
                    label: 'No',
                    //onClick: () => alert('Click No')
                }
            ]
        });
    }
    const deleteRows = async (e) => {
        await deleteDoc(doc(db, db_name, row.original.id)).then(
            meta?.deleteData(row.index)
        )

    }

    return meta?.editedRows[row.id] ? (
        <div className="edit-cell">
            <button onClick={setEditedRows} name="cancel">
                X
            </button>{' '}
            <button onClick={setEditedRows} name="done">
                
            </button>
        </div>
    ) : (
        <div className="edit-cell">
            <button onClick={setEditedRows} name="edit">
                
            </button>
            <button onClick={confirmDelete} name="edit" className="trash">
                
            </button>
        </div>
    );
};
