import React from "react";
import { db, db_name } from "../Database/Firebase";
import { collection, addDoc } from "firebase/firestore";
import ItemModal from "./item_modal";
import "./item.css";

class NewItemButton extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isShown: false
        };
    }
    async createItem(e) {
        e.preventDefault();
        try {
            const docRef = await addDoc(collection(db, db_name), {
                transaction_type: e.target.transaction_type.value,
                name: e.target.name.value,
                amount: e.target.amount.value,
                from: e.target.from.value,
                to: e.target.to.value,
                date: e.target.date.value,
                budget_item: e.target.budget_item.value,
                notes: e.target.notes.value
            });
            console.log("Document written with ID: ", docRef.id);
        } catch (e) {
            console.error("Error adding document: ", e);
        }
        window.location.reload();
    }
    showModal = () => {
        this.setState({ isShown: true }, () => {
        });
        this.toggleScrollLock();
      };
      closeModal = () => {
        this.setState({ isShown: false });
        // this.TriggerButton.focus();
        this.toggleScrollLock();
      };
      onKeyDown = (event) => {
        if (event.keyCode === 27) {
          this.closeModal();
        }
      };
      onClickOutside = (event) => {
        if (this.modal && this.modal.contains(event.target)) return;
        this.closeModal();
      };
    
      toggleScrollLock = () => {
        document.querySelector('html').classList.toggle('scroll-lock');
      };
    render() {
        return (
            <div className='itemButton'>
                <button onClick={this.showModal}>Add Item</button>
                {this.state.isShown ? (
                    <ItemModal
                        onSubmit={this.createItem.bind(this)}
                        modalRef={(n) => (this.modal = n)}
                        buttonRef={(n) => (this.closeButton = n)}
                        closeModal={this.closeModal}
                        onKeyDown={this.onKeyDown}
                        onClickOutside={this.onClickOutside.b}
                    />
                ) : null}
            </div>
        );
    }
}

export default NewItemButton;